/**
 * Deposits Section - Plugin wrapper
 *
 * Admin deposit management with stats, list, and filter controls.
 */

import React, { useEffect, useState } from 'react';
import type { AdminSectionProps } from '../types';
import { AdminDepositStats, AdminDepositList } from '../../components/deposit/admin';

export default function DepositsSection({
  pageSize = 20,
  refreshInterval = 0,
}: AdminSectionProps): React.JSX.Element {
  const [statusFilter, setStatusFilter] = useState<string>('');
  const [refreshSignal, setRefreshSignal] = useState(0);

  useEffect(() => {
    if (refreshInterval <= 0) return;
    const interval = window.setInterval(() => {
      setRefreshSignal((prev) => prev + 1);
    }, refreshInterval);
    return () => window.clearInterval(interval);
  }, [refreshInterval]);

  return (
    <div className="cedros-dashboard__deposits">
      {/* Stats summary */}
      <AdminDepositStats refreshSignal={refreshSignal} />

      {/* Filter and list */}
      <div className="cedros-dashboard__deposits-list">
        <div className="cedros-dashboard__toolbar">
          <div className="cedros-dashboard__filter">
            <label className="cedros-dashboard__filter-label" htmlFor="status-filter">
              Status
            </label>
            <select
              id="status-filter"
              className="cedros-dashboard__select"
              value={statusFilter}
              onChange={(e) => setStatusFilter(e.target.value)}
            >
              <option value="">All statuses</option>
              <option value="pending">Pending</option>
              <option value="detected">Detected</option>
              <option value="processing">Processing</option>
              <option value="completed">Completed</option>
              <option value="withdrawn">Withdrawn</option>
              <option value="expired">Expired</option>
              <option value="failed">Failed</option>
            </select>
          </div>
        </div>

        <AdminDepositList
          statusFilter={statusFilter || undefined}
          pageSize={pageSize}
          refreshSignal={refreshSignal}
        />
      </div>
    </div>
  );
}
